"use client";

import { useState } from "react";
import { motion } from "framer-motion";

export default function Home() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");

  const sendMessage = async (e) => {
    e.preventDefault();
    if (!input.trim()) return;

    const newMessages = [...messages, { role: "user", text: input }];
    setMessages(newMessages);

    try {
      const res = await fetch("/api/chat", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ message: input }),
      });

      const data = await res.json();
      setMessages([
        ...newMessages,
        { role: "bot", text: data.reply || "No reply" },
      ]);
    } catch (err) {
      setMessages([
        ...newMessages,
        { role: "bot", text: "‚ö†Ô∏è Error connecting to server." },
      ]);
    }

    setInput("");
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-black text-white">
      {/* Header */}
      <motion.header
        initial={{ y: -60, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.8 }}
        className="text-center py-10"
      >
        <h1 className="text-4xl font-extrabold tracking-tight">
          üöÄ Farrel‚Äôs Portfolio
        </h1>
        <p className="text-gray-400 mt-2">
          Developer | AI Enthusiast | Designer
        </p>
      </motion.header>

      <main className="px-6 md:px-20 space-y-16">
        {/* About Me */}
        <motion.section
          whileHover={{ scale: 1.03, rotateX: 5, rotateY: -5 }}
          transition={{ type: "spring", stiffness: 200 }}
          className="bg-gradient-to-r from-purple-700/40 to-blue-700/40 p-6 rounded-2xl shadow-2xl backdrop-blur-md"
        >
          <h2 className="text-2xl font-bold mb-2">üëã About Me</h2>
          <p className="text-gray-200">
            Hi, I‚Äôm <span className="text-blue-300">Farrel</span>. Passionate
            about coding, AI, and modern web development.
          </p>
        </motion.section>

        {/* Projects */}
        <motion.section
          whileHover={{ scale: 1.03, rotateX: -5, rotateY: 5 }}
          transition={{ type: "spring", stiffness: 200 }}
          className="bg-gradient-to-r from-pink-600/40 to-red-600/40 p-6 rounded-2xl shadow-2xl backdrop-blur-md"
        >
          <h2 className="text-2xl font-bold mb-2">üí° Projects</h2>
          <ul className="space-y-1 text-gray-200">
            <li>üåê Portfolio Website</li>
            <li>ü§ñ AI Chatbot</li>
            <li>‚ö° Interactive 3D UI</li>
          </ul>
        </motion.section>

        {/* Chat AI */}
        <motion.section
          initial={{ opacity: 0, y: 60 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.9 }}
          className="bg-gradient-to-r from-indigo-700/40 to-purple-700/40 p-6 rounded-2xl shadow-2xl backdrop-blur-md"
        >
          <h2 className="text-2xl font-bold mb-4">üí¨ Chat with My AI</h2>
          <div className="space-y-3 max-h-72 overflow-y-auto p-2 bg-black/20 rounded-lg">
            {messages.map((msg, i) => (
              <motion.div
                key={i}
                initial={{ opacity: 0, x: msg.role === "user" ? 50 : -50 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.4 }}
                className={`p-3 rounded-xl max-w-xs ${
                  msg.role === "user"
                    ? "bg-blue-600 self-end ml-auto"
                    : "bg-gray-700"
                }`}
              >
                {msg.text}
              </motion.div>
            ))}
          </div>

          <form
            onSubmit={sendMessage}
            className="mt-4 flex items-center border border-gray-600 rounded-full overflow-hidden"
          >
            <input
              type="text"
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="Ask me anything..."
              className="flex-grow px-4 py-2 text-gray-200 bg-transparent focus:outline-none"
            />
            <button
              type="submit"
              className="bg-indigo-600 px-4 py-2 text-white font-semibold hover:bg-indigo-700 transition"
            >
              Send
            </button>
          </form>
        </motion.section>
      </main>

      <footer className="text-center text-xs text-gray-500 py-6">
        ¬© {new Date().getFullYear()} Farrel. Built with ‚ù§Ô∏è and AI.
      </footer>
    </div>
  );
}
